@mixin ab($t, $l, $b, $r) {
    position: absolute;    
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }
}
@mixin translate($x, $y) {  
    @include transform(translate($x, $y));
}
@mixin left($l) {
  left: $l;
}
@mixin right($r) {
  right: $r;
} 
@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}
@mixin text-left {
    text-align: start;
}
@mixin flex-end {
    justify-content: flex-end;
}
@mixin flex-start {
    justify-content: flex-start;
}
@mixin border-inline-start($value) {
    border-inline-start:$value;
}  
@mixin border-inline-end($value) {
    border-inline-end:$value;
}

div.cart-popup {
	.cart-header {
		align-items: center;
		.cart-totals {
			align-items: center;
			flex: 0 0 100%;
			margin-top: 15px;
		}
		.totals {
			margin-bottom: 20px;
			align-items: center;
		}
	}
	@media (min-width: 750px) {
		.cart-header .cart-totals {
			flex: 0 0 350px;
			max-width: 350px;
			margin: 0;
			@include padding-end(20px);
		}
	}
}
.cart-popup {
	.cart-empty-inner {
		svg {
			margin-bottom: 24px;
		}
		.h3 {
			font-size: 36px;
			font-weight: 600;
		}
	}
	.cart-table-header {
		display: none;
	}
	.totals__subtotal {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	.cart-table-body {
		.cart-item:not(:last-child) {
			margin-bottom: 20px;
			padding-bottom: 20px;
			border-bottom: 1px solid var(--miscellaneous-color);
		}
	}
	.cart-item {
		position: relative;
	}
	.item-inner .item-media {
		display: none;
	}
	.item-mobile-image {
		flex: 0 0 90px;
		max-width: 90px;
		@include margin-end(15px);
		position: relative;
		cart-remove-button {
			@include ab(0,0,n,n);
			z-index: 1;
			a {
				padding: 10px 10.5px;
				background: var(--miscellaneous-color);
				border-radius: var(--border-radius);
				svg {
					fill: #fff;
				}
			}
		}
	}
	cart-remove-button {
		a {
			display: flex;
			align-items: center;
			width: 30px;
			height: 30px;
			transition: 0.2s all ease-in-out;
			@media (min-width: 750px) and (max-width: 800px) {
				width: auto;
			}
			svg {
				width: 11px;
				height: 11px;
			}
			&:hover {
				opacity: 0.9;
			}
		}
		.loading {
			.icon-loading {
				display: block;
			}
			.icon-remove {
				display: none;
			}
		}
	}
	.item-line-price {
		display: none;
	}
	.cart-item__error {
		display: none;
		align-items: center;
		margin: 10px 0;
	}
	.item-total-price {
		margin-top: 12px;
	}
	.cart-item__quantity-wrapper {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
	}
	.cart-item__quantity {
		order: 5;
	}
	.quantity-select {
		background: #F0F0F0;
		max-width: 96px;
		display: flex;
		position: relative;
		border-radius: var(--border-radius);
		input::-webkit-outer-spin-button,
		input::-webkit-inner-spin-button {
			appearance: none;
			margin: 0;
		}
		input {
			width: calc(100% - 64px);
			padding: 0;
			display: flex;
			align-items: center;
			justify-content: center;
			text-align: center;
			font-size: 15px;
			height: 32px;
		}
		button {
			padding: 10px;
			width: 32px;
			height: 32px;
			display: flex;
			align-items: center;
			justify-content: center;
			.icon-minus {
				width: 7px;
				height: 7px;
			}
		}
		.icon-loading {
			display: none;
			width: 15px;
			height: 15px;
			@include ab(50%,50%,n,n);
			@include margin-start(-7.5px);
			margin-top: -7.5px;
		}
		&.loading {
			.icon-loading {
				display: block;
			}
			input {
				opacity: 0;
				visibility: hidden;
			}
		}
	}
	.item-detail {
		margin: 0 0 10px;
		a {
			color: var(--text-color);
			font-size: 13px;
		}
	}
	.table-header {
		padding: 20px 15px;
		text-transform: uppercase;
		font-weight: 500;
		background: #f9f8f8;
	}
	.table-col-product {
		flex: 0 0 40%;
		max-width: 40%;
	}
	.table-col-price {
		flex: 0 0 20%;
		max-width: 20%;
		text-align: center;
	}
	.table-col-quantity {
		flex: 0 0 20%;
		max-width: 20%;
		text-align: center;
	}
	.table-col-total {
		flex: 0 0 20%;
		max-width: 20%;
		text-align: center;
	}
	.cart-footer {
		padding: 10px 20px 20px;
		@include ab(n,0,0,0);
		background: var(--background-color);
		z-index: 1;
		.cart-buttons {
			.btn {
				display: flex;
				align-items: center;
				text-align: center;
				justify-content: center;
				width: 100%;
				margin-top: 12px;
			}
		}
	}
	@media (min-width: 750px) {
		.cart-table {
			border: 1px solid var(--miscellaneous-color);
			border-radius: var(--border-radius);
			max-height: 433px;
			overflow: hidden;
			overflow-y: auto;
		}
		.cart-table::-webkit-scrollbar-track {
			box-shadow: inset 0 0 4px var(--miscellaneous-color);
			-webkit-box-shadow: inset 0 0 var(--miscellaneous-color);
			background-color: var(--miscellaneous-color);
			border-radius: 5px;
		}
		.cart-table::-webkit-scrollbar {
			width: 4px;
			background-color: var(--miscellaneous-color);
			border-radius: 5px;
		}
		.cart-table::-webkit-scrollbar-thumb {
			background-color: var(--primary-color);
			border-radius: 5px;
		}
		.cart-table-header {
			display: flex;
			border-bottom: 1px solid var(--miscellaneous-color);
			>div:not(:last-child) {
				@include border-inline-end(1px solid var(--miscellaneous-color));
			}
		}
		.cart-item__quantity-wrapper {
			justify-content: center;
		}
		.item-detail {
			font-size: 15px;
			margin-bottom: 0;
			flex: 1;
		}
		.item-inner {
			display: flex;
			width: 100%;
			.item-media {
				display: block;
			}
			>div {
				padding: 10px;
			}
			>* {
				&:not(:last-child) {
					@include border-inline-end(1px solid var(--miscellaneous-color));
				}
			}
			cart-remove-button {
				flex: 0 0 30px;
			}
		}
		.cart-table .cart-table-body .cart-item {
			padding-bottom: 0;
			margin-bottom: 0;
		}
		.item-line-price {
			display: block;
		}
		.item-header {
			flex: 0 0 40%;
			max-width: 40%;
			display: flex;
			align-items: center;
			.item-media {
				flex: 0 0 80px;
				max-width: 80px;
				@include margin-end(15px);
			}
		}
		.item-line-price {
			flex: 0 0 20%;
			max-width: 20%;
			display: flex;
			align-items: center;
			justify-content: center;
		}
		.item-qty {
			flex: 0 0 20%;
			max-width: 20%;
			display: flex;
			align-items: center;
			justify-content: space-between;
			order: unset;
		}
		.item-total-price {
			flex: 0 0 20%;
			max-width: 20%;
			display: flex;
			align-items: center;
			justify-content: center;
			margin-top: 0;
		}
		.cart-footer {
			.cart-buttons {
				display: grid;
				grid-template-columns: 1fr 1fr;
				grid-gap: 20px;
				max-width: 460px;
				@include margin-start(auto);
			}
		}
	}
	@media (max-width: 749px) {
		.popup-inner {
			overflow: hidden;
			height: 100%;
			@include padding-end(15px);
		}
		.cart-table {
			height: 90vh;
		}
		.cart-table-body {
			height: calc(100% - 270px);
			overflow: auto;
		}
		.product-title {
			@include padding-end(10px);
		}
		.item-inner {
			cart-remove-button {
				display: none;
			}
		}
	}
}